export function ShowToast(msg, style, obj) {
  const toastOptions = {
    icon: {
      name: "mdi-information-outline",
    },
    iconPack: "mdi",
    theme: "toasted-primary", // "outline", "bubble",
    position: "bottom-center",
    duration: 3000,
    action: {
      text: "Close",
      onClick: (e, toastObject) => {
        toastObject.goAway(0);
      },
    },
    closeOnSwipe: true,
    singleton: true,
  };
  switch (style) {
    case "info":
      obj.$toasted.info(msg, toastOptions);
      break;
    case "success":
      obj.$toasted.success(msg, toastOptions);
      break;
    case "error":
      obj.$toasted.error(msg, toastOptions);
      break;
  }
}

