export async function ProcessData(url, m, data) {
  try {
    const response = await fetch(url, {
      method: m,
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });
    const json = await response.json();

    return {
      output: json,
      status: response.status,
    };
  } catch (error) {
    //console.log('ProcessData data: ', url, m, JSON.stringify(data))
    console.log("ProcessData API error: " + JSON.stringify(error));
    return {
      error: error,
      status: "404",
    };
  }
}
