import { ProcessData } from "@/api/utils";

const REST_URL = process.env.VUE_APP_BASE_URL;

export function GetAllProducts() {
  return ProcessData(`${REST_URL}/api/v1/products`, "GET");
}

export function CreateProduct(product) {
  return ProcessData(`${REST_URL}/api/v1/admin`, "POST", product);
}

export function UpdateProduct(product) {
  return ProcessData(`${REST_URL}/api/v1/admin`, "PATCH", product);
}

export function DeleteProducts(productID) {
  return ProcessData(`${REST_URL}/api/v1/products`, "DELETE", productID);
}
