<template>
  <v-div
    ><v-data-table
      :headers="productHeaders"
      :items="products"
      :items-per-page="5"
      :search="search"
      class="elevation-1"
    >
      <template v-slot:top>
        <div class="tw-flex tw-justify-between tw-items-center tw-mx-4">
          <div class="tw-w-1/4 tw-mr-auto tw-mt-1">
            <v-text-field
              v-model="search"
              class="tw-p-0"
              label="Search"
              prepend-inner-icon="mdi-magnify"
              clearable
            ></v-text-field>
          </div>
          <div>
            <v-btn outlined dense @click="showProductForm()"> New </v-btn>
          </div>
        </div>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline"
              >Are you sure you want to delete this item?</v-card-title
            >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancel</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteProductConfirm"
                >Yes</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </template>

      <template v-slot:item="row">
        <tr>
          <td class="tw-border-none text-capitalize">
            {{ row.item.productName }}
          </td>
          <td class="tw-border-none text-capitalize">
            {{ row.item.productDescription }}
          </td>
          <td class="tw-border-none text-capitalize">
            {{ row.item.price }}
          </td>
          <td class="tw-border-none text-capitalize">
            {{ row.item.offerPrice }}
          </td>
          <!-- <td class="tw-border-none">
            <v-icon color="green" v-if="row.item.published">
              mdi-check-bold
            </v-icon>
            <v-icon v-else> mdi-close-thick </v-icon>
          </td> -->
          <td class="tw-border-none">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon color="blue" v-bind="attrs" v-on="on" class="mr-2">
                  <v-icon @click="handleProductEditClick(row.item)">
                    mdi-eye-plus
                  </v-icon>
                </v-btn>
              </template>
              <span>View/Edit</span>
            </v-tooltip>

            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon color="red" v-bind="attrs" v-on="on">
                  <v-icon @click="handleProductDeleteClick(row.item)">
                    mdi-delete
                  </v-icon>
                </v-btn>
              </template>
              <span>Delete</span>
            </v-tooltip>
          </td>
        </tr>
      </template>
    </v-data-table>
  </v-div>
</template>
<script>
import { ShowToast } from "@/helpers/toast";
import { GetAllProducts, DeleteProduct } from "@/api/master";
export default {
  name: "Product",
  data() {
    return {
      search: "",
      dialogDelete: false,
      deleteItem: {},
      productHeaders: [
        { text: "Name", align: "start", value: "productName" },
        { text: "Image", value: "productImg", sortable: false },
        { text: "Price", value: "price" },
        { text: "Best Sellers", value: "" },
        { text: "New Items", value: "" },
      ],
      products: [],
    };
  },
  mounted() {
    this.getProduct();
  },
  methods: {
    getProduct() {
      GetAllProducts().then((response) => {
        this.products = response.output;
      });
    },
    deleteProductConfirm() {
      DeleteProduct(this.deleteItem.id).then((response) => {
        if (response.status === 200) {
          ShowToast("Product deleted : " + this.deleteItem.name, "info", this);
        } else {
          ShowToast("Delete operation failed", "error", this);
        }
        this.dialogDelete = false;
        this.getProduct();
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.deleteItem = {};
      });
    },
    showProductForm() {
      this.$router.push("/admins");
    },
    handleProductEditClick(item) {
      this.$emit("handle-Productedit", item);
    },
    handleProductDeleteClick(item) {
      this.dialogDelete = true;
      this.deleteItem = Object.assign({}, item);
    },
  },
};
</script>
