<template>
  <v-div>
    <h1>Hello v-model="$v.productName.$model"</h1>
    <section>
      <v-row no-gutters justify="center" class="tw-w-full tw-pa-0">
        <v-col cols="12">
          <!-- Tool bar -->
          <v-row no-gutters class="tw-w-full tw-pa-0">
            <v-col cols="12" class="tw-h-10 tw-bg-gray-200">
              <div class="tw-w-full">
                <v-row no-gutters>
                  <v-spacer />
                  <v-btn
                    :disabled="buttonDisabled"
                    text
                    @click.prevent="handleCloseProductform()"
                  >
                    <span>Cancel</span>
                  </v-btn>
                  <v-btn
                    :disabled="buttonDisabled"
                    text
                    @click.prevent="handleSaveProduct()"
                  >
                    <span>Save</span>
                  </v-btn>
                </v-row>
              </div>
            </v-col>
          </v-row>
          <v-row align="start" justify="center" class="pa-0 mx-auto tw-w-full">
            <v-col cols="9">
              <v-row
                align="start"
                justify="center"
                class="pa-0 mx-auto tw-w-full"
              >
                <v-col>
                  <!-- Product Name -->
                  <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold">
                        Product Name
                      </span>
                      <v-text-field
                        type="text"
                        placeholder="Product Name"
                        class="mt-0"
                        outlined
                        hide-details="auto"
                        :error-messages="productNameErrors"
                        @input="handleProductNameInput()"
                        @blur="handleProductNameInput()"
                      />
                    </v-list-item-content>
                  </v-list-item>

                  <!-- <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold">
                        Product Description
                      </span>
                      <v-text-field
                        type="text"
                        placeholder="Product Description"
                        class="mt-0"
                        outlined
                        hide-details="auto"
                        :error-messages="productDescriptionErrors"
                        @input="handleProductDescriptionInput()"
                        @blur="handleProductDescriptionInput()"
                      />
                    </v-list-item-content>
                  </v-list-item> -->

                  <!-- Product Description -->

                  <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold"
                        >Product Description
                      </span>
                      <v-textarea
                        v-model="productDescription"
                        id="productDescription"
                        name="productDescription"
                        class="mt-0"
                        outlined
                        hide-details="auto"
                        :error-messages="productDescriptionErrors"
                        @input="handleProductDescriptionInput()"
                        @blur="handleProductDescriptionInput()"
                      />
                    </v-list-item-content>
                  </v-list-item>

                  <!-- Product Image -->

                  <!-- <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold">
                        Product Image
                      </span>
                      <v-text-field
                        id="productImg"
                        name="Product Image"
                        class="mt-0"
                        hide-details="auto"
                        :error-messages="productImgErrors"
                        :src="productImg"
                        @input="handleProductImg()"
                        @blur="handleProductImg()"
                      ></v-text-field>
                    </v-list-item-content>
                  </v-list-item> -->

                  <!-- Price -->

                  <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold"> Price </span>
                      <v-text-field
                        type="number"
                        placeholder="Price"
                        class="mt-0"
                        outlined
                        hide-details="auto"
                        :error-messages="priceErrors"
                        @input="handlePriceInput()"
                        @blur="handlePriceInput()"
                      />
                    </v-list-item-content>
                  </v-list-item>

                  <!-- Offer Price -->

                  <v-list-item two-line>
                    <v-list-item-content>
                      <span class="tw-text-sm tw-font-bold"> Offer Price </span>
                      <v-text-field
                        type="number"
                        placeholder="Offer Price"
                        class="mt-0"
                        outlined
                        hide-details="auto"
                        :error-messages="offerPriceErrors"
                        @input="handleOfferPriceInput()"
                        @blur="handleOfferPriceInput()"
                      />
                    </v-list-item-content>
                  </v-list-item>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-col>
      </v-row></section
  ></v-div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { ShowToast } from "@/helpers/toast";
import { CreateProduct, UpdateProduct } from "@/api/master";

export default {
  name: "ProductForm",
  props: {
    isUpdate: {
      type: Boolean,
      required: false,
    },
    productItem: {
      type: Object,
      required: false,
    },
  },
  data() {
    return {
      buttonDisabled: false,
      productName: "",
      productDescription: "",
      price: "",
      offerPrice: "",
    };
  },
  mixins: [validationMixin],
  validations: {
    productName: { required },
    productDescription: { required },
    price: { required },
  },
  mounted() {
    this.getValues();
  },
  computed: {
    //   productNameErrors() {
    //     const errors = [];
    //     if (!this.$v.productName.$dirty) return errors;
    //     !this.$v.productName.required && errors.push("Product Name required.");
    //     return errors;
    //   },
    //   productDescriptionErrors() {
    //     const errors = [];
    //     if (!this.$v.productDescription.$dirty) return errors;
    //     !this.$v.productDescription.required &&
    //       errors.push("Product Description required.");
    //     return errors;
    //   },
    //   priceErrors() {
    //     const errors = [];
    //     if (!this.$v.price.$dirty) return errors;
    //     !this.$v.price.required && errors.push("Product Price required.");
    //     return errors;
    //   },
  },
  methods: {
    getValues() {
      if (this.isUpdate === false) return;
      else {
        this.productName = this.productItem.name;
        this.productDescription = this.productItem.desc;
        this.price = this.productItem.price;
        this.offerPrice = this.productItem.offerPrice;
      }
    },
    async handleSaveProduct() {
      this.$v.$touch();
      if (this.$v.$invalid) return;
      this.buttonDisabled = true;
      const product = {
        name: this.productName,
        desc: this.productDescription,
        price: this.price,
        offerPrice: this.offerPrice,
      };
      if (this.isUpdate == false) {
        let response = await CreateProduct(product);
        if (response.status === 200) {
          ShowToast("Created new Product," + this.name, "success", this);
        } else {
          ShowToast(response.output.message, "error", this);
        }
      } else {
        let response = await UpdateProduct(product);
        if (response.status === 200) {
          ShowToast("Updated the Product " + this.name, "success", this);
        } else {
          ShowToast(response.output.message, "error", this);
        }
      }
      this.handleCloseProductform();
    },
    handleCloseProductform() {
      this.$router.push("/admin");
    },

    handleTargetrolesChipClose(role) {
      for (let i = 0; i < this.targetRoles.length; i++) {
        const element = this.targetRoles[i];
        if (element === role.id) this.targetRoles.splice(i, 1);
      }
    },

    handleProductNameInput() {
      this.isError = false;
      this.errorText = "";
      this.$v.productName.$touch();
    },

    handleProductDescriptionInput() {
      this.isError = false;
      this.errorText = "";
      this.$v.productDescription.$touch();
    },

    handlePriceInput() {
      this.isError = false;
      this.errorText = "";
      this.$v.priceInput.$touch();
    },

    handleOfferPriceInput() {
      this.isError = false;
      this.errorText = "";
      this.$v.offerPriceInput.$touch();
    },
  },
};
</script>
